import { Suppliers as sp, Products as pr, Users as us } from './Types';
import { Requests } from '../../Services';

type component = "supplier" | "product" | "user";

export const Filter = (filter:any,component:component) => {

        let params = '';
        Object.keys(filter).forEach( (a:any) => {
            if(filter[a].key){
                if(params === ''){
                    params = a+"[filter]="+filter[a].filter+"&"+a+"[key]="+filter[a].key;
                }else{
                    params = params+"&"+a+"[filter]="+filter[a].filter+"&"+a+"[key]="+filter[a].key;
                }
            }
        });

        switch(component){
            case "supplier" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : sp,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : sp,
                        payload : await Requests.Suppliers.filter(params),
                    });
                }
            case "product" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : pr,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : pr,
                        payload : await Requests.Products.filter(params),
                    });
                }
            case "user" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : us,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : us,
                        payload : await Requests.User.filter(params),
                    });
                }
        }

        
}
