import { TableRow, TableCell, Popper, Paper, Typography, TextField, Button, FormControl, InputLabel, Select, MenuItem } from "@material-ui/core"
import React, { useState } from "react";
import { DatePicker } from "material-ui-pickers";
import moment from "moment";
import {Filter as Filterer} from "Redux/Actions";
import {useDispatch} from 'react-redux';


const Filter = () => {



    const dispatch = useDispatch();

    type control = "supplier" | "product_code" | "product_description" | "serial_number" | "warranty" | "warranty_start" | "warranty_end";
    type action = "close" | "open";
    type submit = "clear" | "filter";


    const [popper,setPopper] = useState({
        supplier            : null,
        product_code        : null,
        product_description : null,
        serial_number       : null,
        warranty            : null,
        warranty_start      : null,
        warranty_end        : null,
    });

    const [filter, setFilter] = useState({
        supplier            : {filter:'iet',key:''},
        product_code        : {filter:'iet',key:''},
        product_description : {filter:'iet',key:''},
        serial_number       : {filter:'iet',key:''},
        warranty            : {filter:'iet',key:''},
        warranty_start      : {filter:'iet',key:''},
        warranty_end        : {filter:'iet',key:''}
    });


    const reset = () => {
        
        let a = popper;
        a.supplier              = null;
        a.product_code          = null;
        a.product_description   = null;
        a.serial_number         = null;
        a.warranty              = null;
        a.warranty_end          = null;
        a.warranty_start        = null;
        a.warranty_end          = null;
        setPopper(a);
    }
    
    const controller = (event:any,control:control,action:action) => {

        reset();
        
        if(action === "open"){
            setPopper({
                ...popper,
                [control] : event.currentTarget,
            })
        }else{
            setPopper({
                ...popper,
                [control] : null,
            })
        }
    }

    const setText=(event:any) =>{
        let a:control = event.target.name;
        setFilter({
            ...filter,
            [event.target.name] : {
                ...filter[a],
                key : event.target.value,
            }
        });
    }

    const setFilterControl=(event:any) =>{
        let a:control = event.target.name;
        setFilter({
            ...filter,
            [event.target.name] : {
                ...filter[a],
                filter : event.target.value,
            }
        });
    }

    const submitFilter = async (action:submit,control:control) =>{
        reset();
        if(action === "filter"){
            if(filter[control].key !== ''){
                dispatch(Filterer(filter,"product"));
            }
        }else{
            if(filter[control].key !== ''){
                let a = filter;

                setFilter({
                    ...filter,
                    [control] : {
                        ...filter[control],
                        key : ''
                    }
                });
                
                a[control].key = "";
                dispatch(Filterer(a,"product"));
            }
        }
    }

    const onKey = (e:any) => {

        if(e.key === "Backspace"){
            if(e.target.value.length === 1){
                reset();
                
                let a:any = filter;
                a[e.target.name].key = '';    
                dispatch(Filterer(a,"product"));
            }
        }

        if(e.key === 'Enter'){
            if(e.target.value !== ''){
                reset();
                dispatch(Filterer(filter,"product"));
            }
        }
    }


    return(
        <TableRow>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "supplier"
                    value = {filter.supplier.key}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"supplier","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.supplier)? "simple-popper" : undefined} open={Boolean(popper.supplier)} anchorEl={popper.supplier}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "supplier"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","supplier")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","supplier")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>

            </TableCell>

            <TableCell align="center" className="filter">
                
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "product_code"
                    value = {filter.product_code.key}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"product_code","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.product_code)? "simple-popper" : undefined} open={Boolean(popper.product_code)} anchorEl={popper.product_code}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "product_code"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","product_code")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","product_code")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>

            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "product_description"
                    value = {filter.product_description.key}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"product_description","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.product_description)? "simple-popper" : undefined} open={Boolean(popper.product_description)} anchorEl={popper.product_description}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "product_description"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","product_description")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","product_description")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "serial_number"
                    value = {filter.serial_number.key}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"serial_number","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.serial_number)? "simple-popper" : undefined} open={Boolean(popper.serial_number)} anchorEl={popper.serial_number}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "serial_number"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","serial_number")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","serial_number")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    type = "number"
                    name = "warranty"
                    value = {filter.warranty.key}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"warranty","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.warranty)? "simple-popper" : undefined} open={Boolean(popper.warranty)} anchorEl={popper.warranty}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "warranty"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","warranty")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","warranty")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <DatePicker
                    format="yyyy/MM/dd"
                    allowKeyboardControl
                    animateYearScrolling
                    variant = "outlined"
                    className="input"
                    onChange = {(event) => setFilter({
                            ...filter,
                            warranty_start: {
                                ...filter['warranty_start'],
                                key : moment(event).format("YYYY-MM-DD")
                            }
                        }
                    )}
                    value = {filter.warranty_start.key == '' ? null : moment(filter.warranty_start.key).format()}
                    onFocus = {(event)=>controller(event,"warranty_start","open")}
                    // onKeyDown = {onKey}
                />


                
                <Popper className="text-center" id={Boolean(popper.warranty_start)? "simple-popper" : undefined} open={Boolean(popper.warranty_start)} anchorEl={popper.warranty_start}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "warranty_start"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","warranty_start")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","warranty_start")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <DatePicker
                    format="yyyy/MM/dd"
                    allowKeyboardControl
                    animateYearScrolling
                    variant = "outlined"
                    className="input"
                    onChange = {(event) => setFilter({
                            ...filter,
                            warranty_end:{
                                ...filter['warranty_end'],
                                key : moment(event).format("YYYY-MM-DD")
                            }
                        }
                    )}
                    value = {filter.warranty_end.key == '' ? null : moment(filter.warranty_end.key).format()}
                    onFocus = {(event)=>controller(event,"warranty_end","open")}
                    // onKeyDown = {onKey}
                />


                
                <Popper className="text-center" id={Boolean(popper.warranty_end)? "simple-popper" : undefined} open={Boolean(popper.warranty_end)} anchorEl={popper.warranty_end}>
                    <Paper className="popper-paper">
                        <FormControl variant="outlined" className="selector">
                            <Select
                                value={filter.supplier.filter}
                                onChange={setFilterControl}
                                name = "warranty_end"
                            >
                                <MenuItem value="iet" selected>Is Equal To </MenuItem>
                                <MenuItem value="inet">Is Not Equal To</MenuItem>
                                <MenuItem value="c">Contains</MenuItem>
                                <MenuItem value="dnc">Does not Contains</MenuItem>
                                <MenuItem value="sw">Starts With</MenuItem>
                                <MenuItem value="ew">Ends With</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","warranty_end")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","warranty_end")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
        </TableRow>
    );
}

export default Filter;